import React, { useContext, useState } from 'react';
import { useNavigate } from 'react-router-dom';
import '../css/First/FirstPage.css'; //   
import WeekDaysSlider from '../utils/weekDaysSlider';
import BannerSlider from '../utils/bannerSlider';
import { AuthContext } from '../contexts/AuthContext';
import RunningList from './RunningPage';



const FirstPage = () => {
    const navigate = useNavigate();
    const { user, logout } = useContext(AuthContext); // AuthContext     
    const [selectedDate, setSelectedDate] = useState(''); // State to hold selected date
    const [selectedPage, setSelectedPage] = useState(''); // State to hold selected page



    const handlePageChange = (path, page) => {
        setSelectedPage(page);
        navigate(path);
    }

    const handleDateClick = (formattedDate) => {
        setSelectedDate(formattedDate); // Update selected date
    }


    return (
        <div className='container'>
            <div className='navbar'>
                <div className='userMenu'>
                    <div>
                        <h1 onClick={() => handlePageChange('/')} className='clickable-text'>RUNNING CREW</h1>
                    </div>

                    <div className='searchBox'>
                        <input type="text"
                            placeholder='  '
                            className='searchInput'/>

                        <button
                            id='search-btn' className='search-btn'>
                            <img src='/images/searchImage.png' alt='search icon' className='search-Img'/>
                        </button>
                        </div>

                        <div className='login'>
                        {user ? (
                            <>
                                <p onClick={() => handlePageChange('/mypage')} className='clickable-text'>{user.nickname}</p>
                                <button onClick={logout}></button>
                            </>
                        ) : (
                            <h1 onClick={() => handlePageChange('/login')} className='clickable-text'></h1>
                        )}
                    </div>
                </div>
            </div>

            <div className='navigation__wrapper'>
                <div className={`navigation__item ${selectedPage === '' ? 'on' : ''}`}>
                    <h2 onClick={() => handlePageChange('/', '')} className='clickable-text'></h2>
                </div>
                <div className={`navigation__item ${selectedPage === '' ? 'on' : ''}`}>
                    <h2 onClick={() => handlePageChange('/crew', '')} className='clickable-text'></h2>
                </div>
                <div className={`navigation__item ${selectedPage === '' ? 'on' : ''}`}>
                    <h2 onClick={() => handlePageChange('/chalange', '')} className='clickable-text'></h2>
                </div>
                <div className={`navigation__item ${selectedPage === '' ? 'on' : ''}`}>
                    <h2 onClick={() => handlePageChange('/create/running', '')} className='clickable-text'></h2>
                </div>
            </div>
           
            <div>
                <BannerSlider />
            </div>
            <div>
                <RunningList  />
            </div>

        </div>
               

        
     
    );
};

export default FirstPage;