import React, { useState } from 'react';
import { useNavigate } from 'react-router-dom';
import axiosInstance from '../utils/axiosInstance';
import DaumPostcode from "react-daum-postcode";
import Modal from 'react-modal';
import '../css/Running/RunningCreatePage.css';
import '../css/Common.css';

Modal.setAppElement('#root');

const App = () => {
  const [step, setStep] = useState(1); //  
  const [title, setTitle] = useState('');
  const [content, setContent] = useState('');
  const [distance, setDistance] = useState('');
  const [startLocation, setStartLocation] = useState('');
  const [startDetailLocation, setStartDetailLocation] = useState('');
  const [finishLocation, setFinishLocation] = useState('');
  const [finishDetailLocation, setFinishDetailLocation] = useState('');
  const [limitedPeople, setLimitedPeople] = useState('');
  const [date, setDate] = useState('');
  const [time, setTime] = useState('');
  const [message, setMessage] = useState('');
  const [modalIsOpen, setModalIsOpen] = useState(false);
  const [addressType, setAddressType] = useState(''); // :    



  const navigate = useNavigate();

  const handleNext = () => {
    setStep(step + 1);
  };

  const handlePrevious = () => {
    setStep(step - 1);
  };

  const handleAddressComplete = (data) => {
    if(addressType === 'start') {
      setStartLocation(data.address);
    }else if(addressType === 'finish') {
      setFinishLocation(data.address);
    }
    closeModal();
  };

  const handleSubmit = async (event) => {
    event.preventDefault();

    try {
      const response = await axiosInstance.post('/api/runnings/create', {
        title,
        content,
        distance,
        startLocation,
        startDetailLocation,
        finishLocation,
        finishDetailLocation,
        limitedPeople,
        date,
        time
      });

      if (response.status === 200) {
        setMessage(response.data);
        alert(' ~');
        navigate('/');
      }
    } catch (error) {
      setMessage(error.response.data.errorMessage);
      alert(error.response.data.errorMessage);
    }
  };

  const openModal = (type) => {
    setModalIsOpen(true);
    setAddressType(type);
  };

  const closeModal = () => {
    setModalIsOpen(false);
  };

  return (
    <div className='card'>
      <div className='card-header'>
        <h2 className='header-main clickable-text' onClick={() => navigate("/")}>RUNNING CREW</h2>
        <h2 className='header-sub'> </h2>
      </div>

      {step === 1 && (
        <form onSubmit={handleSubmit}>
          <div className='card-write'>
            <div className='title-w'>
              <label> :</label>
              <input
                type="text"
                value={title}
                onChange={(e) => setTitle(e.target.value)}
                placeholder=" ."
              />
            </div>

            <div className='content-w'>
              <label> :</label>
              <textarea
                placeholder=',    .'
                className='content-input'
                type="text"
                value={content}
                onChange={(e) => setContent(e.target.value)}
              />
            </div>
          </div>

          <button type="button" onClick={handleNext}></button>
        </form>
      )}

      {step === 2 && (
        <form onSubmit={handleSubmit}>
          <div className='card-write'>
            <div>
              <label>:</label>
              <input
                className='distance-input'
                placeholder='  . ( km)'
                type="number"
                step="0.1"
                value={distance}
                onChange={(e) => setDistance(e.target.value)}
              />
            </div>

            <div>
              <label>:</label>
              <input
                type="text"
                value={startLocation}
                onClick={() => openModal('start')}
                onChange={(e) => setStartLocation(e.target.value)}
              />
            </div>

            <div>
              <label className='detail-address'> :</label>
              <input
                type="text"
                value={startDetailLocation}
                onChange={(e) => setStartDetailLocation(e.target.value)}
              />
            </div>

            <div>
              <label>:</label>
              <input
                type="text"
                onClick={() => openModal('finish')}
                value={finishLocation}
                onChange={(e) => setFinishLocation(e.target.value)}
              />
            </div>

            <div>
              <label className='detail-address'> :</label>
              <input
                type="text"
                value={finishDetailLocation}
                onChange={(e) => setFinishDetailLocation(e.target.value)}
              />
            </div>
          </div>
          <button type="button" onClick={handlePrevious}></button>
          <button type="button" onClick={handleNext}></button>
        </form>
      )}

      {step === 3 && (
        <form onSubmit={handleSubmit}>
          <div className='card-write'>
            <div>
              <label> :</label>
              <input
                className='distance-input'
                placeholder=' .'
                type="number"
                step="0"
                value={limitedPeople}
                onChange={(e) => setLimitedPeople(e.target.value)}
              />
            </div>

            <div>
              <label> :</label>
              <input
                type="date"
                value={date}
                onChange={(e) => setDate(e.target.value)}
              />
            </div>

            <div>
          <label> :</label>
          <input
            type="time"
            value={time}
            onChange={(e) => setTime(e.target.value)}
          />
        </div>

          </div>

          <button type="button" onClick={handlePrevious}></button>
          <button type="submit"></button>
        </form>
      )}

      <p>{message}</p>

      <Modal
        isOpen={modalIsOpen}
        onRequestClose={closeModal}
        contentLabel=" "
      >
        <h2> </h2>
        <DaumPostcode onComplete={handleAddressComplete}/>

        <button onClick={closeModal}></button>
      </Modal>
    </div>
  );
};

export default App;
