import React from 'react';
import Slider from 'react-slick';
import "slick-carousel/slick/slick.css"; 
import "slick-carousel/slick/slick-theme.css";
import '../css/WeekDaysSlider.css'

const WeekDays = () => {
  const currentDate = new Date();
  const week = ['', '', '', '', '', '', ''];
  const daysToShow = 14; // 14  

  const days = [];
  for (let i = 0; i < daysToShow; i++) {
    const date = new Date();
    date.setDate(currentDate.getDate() + i);

    const formattedDate = `${date.getDate()}`;
    const dayOfWeek = week[date.getDay()];



    days.push(
      <div key={i} className="dateWrap isSun clickable-text">
        <p>{formattedDate}</p>
        <span>{dayOfWeek}</span>
      </div>
    );
  }


  //      
  const settings = {
    dots: true,
    infinite: true,
    speed: 500,
    slidesToShow: 7, //    
    slidesToScroll: 7, //     
    initialSlide: 0,
    responsive: [
      {
        breakpoint: 1024,
        settings: {
          slidesToShow: 5,
          slidesToScroll: 1,
          infinite: true,
          dots: true,
        },
      },
      {
        breakpoint: 600,
        settings: {
          slidesToShow: 3,
          slidesToScroll: 1,
          initialSlide: 2,
        },
      },
      {
        breakpoint: 480,
        settings: {
          slidesToShow: 2,
          slidesToScroll: 1,
        },
      },
    ],
  };

  return (
    <div>
      <Slider {...settings}> {/*     */}
        {days}
      </Slider>
    </div>
  );
};

export default WeekDays;
